<template>
  <div>
    <div class="serach">
  <el-date-picker
      v-model="value1"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>

  <el-button type="primary" style="margin-left: 15px;" @click="getrange">查询时间段</el-button>
    </div>
    
   <article 
    v-for="item in list"
       :key="item.id"
   >
    <header class="post-header">
      <div class="post-meta-container">
        <span>
           时间:
        </span>
        <time>
     {{item.time}}
        </time>
      </div>

      <div class="post-title" >
            <router-link class="post-title-link" :to="`/Article/${item.pathID}`" >
            <span itemprop="name">{{item.title}}</span>
            </router-link> 
      </div>

      
    </header>
  </article>
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page="queryinfo.pagenum"
          layout="total, prev, pager, next"
        :page-size="queryinfo.pagesize"
        :total="total"
        background
      >
      </el-pagination>
<el-button type="primary" class="plus" icon="el-icon-plus" circle @click='goedit'></el-button>
  </div>
</template>
<script>

export default {
  
  data() {
    return {
      list:[
      ],
     total:0,
      queryinfo: {
        pagenum: 1,
        pagesize:5,
      },
      value1:''
 
      }
  },
  created(){
    const Firstlist= this.$store.state.list
    this.list=Firstlist.slice(0,this.queryinfo.pagesize)
     this.total= Firstlist.length;

  },
  methods: {
    goedit(){
this.$router.push({ path:'Article/Edit' })
    },
    getrange(){
      this.list=[]
      var start=this.value1[0].getTime()
      var end=this.value1[1].getTime()
        const list=this.$store.state.list
        list.forEach(item => {
          console.log(item.time_stamp);
        if(parseInt(start) < parseInt(item.time_stamp) && parseInt(end) > parseInt(item.time_stamp)){
          console.log(item);
          this.list.push(item)
        }  
        });
        // console.log(end);
    },
   handleCurrentChange(newpage) {
    const Firstlist= this.$store.state.list
     console.log(newpage*5);
     var start;
    newpage!==1 ? start=this.queryinfo.pagesize : start=0
      this.list=Firstlist.slice(start,newpage*5)
      this.queryinfo.pagenum = newpage;
    },
  },
};
</script>
<style lang="less" scoped>
.el-pagination {
    text-align: center; 
}
.plus{
    position:fixed;
    right:100px;
    top:100px;

}
.serach{
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}
.el-input__inner{
background-color: var(--content-bg-color);
}

</style>