<template>
  <div>
<article>
<span hidden itemprop="publisher" >
      <meta itemprop="name" content="NexT">
    </span>
        <h2 class="post-title" itemprop="name headline">
          
              <span class="exturl post-title-link post-title-link-external" data-url="aHR0cHM6Ly9naXRodWIuY29tL25leHQtdGhlbWU=" itemprop="url">
            {{article.title}}
              </span>
          
        </h2>

        <div class="post-meta-container">
          <div class="post-meta">
          <span class="post-meta-item">
          <span >
           时间：
          </span>

         <time  itemprop="dateCreated datePublished" > {{article.time}}</time>
   
          </span>
          </div>

      
        </div>
    <div v-html="article.contexthtml"></div>


</article>


    
   </div>
</template>
<script>

export default {
  data() {
    return {
     title:'',
     article:{}
    };
  },
  created(){
  const list=this.$store.state.list
   var id=this.$route.params.id;
list.forEach(item => {
  if(`${item.id}` === id){
    this.article={
      time:item.time,
      contexthtml:item.contexthtml,
      title:item.title
    }
  }
});
      
  },
  methods: {
  
  },
};
</script>
<style lang="less" scoped>
.post-title{
  text-align: center;
}

</style>