<template>
  <div>
<quill-editor ref="myTextEditor"
 v-model="content" 
 :options="editorOption" 
style=" margin-bottom: 10px"
></quill-editor>
<el-button type="primary" @click="saveHtml">保存</el-button>
   </div>
</template>
<script>
export default {
  data() {
    return {
        //富文本内容
      content: "",
      editorOption: {
        
      }
    };
  },
  created(){
this.editorOption=require('./Edit.json')
  },
  methods: {
 
  saveHtml(){
  
    var timeStamp = new Date().getTime()
      var data=this.$options.filters.dataFormat(new Date())
    this.$store.commit('add',[this.content,data,timeStamp])
        var id= this.$store.state.list.length-1
        console.log(id);
   this.$router.push({ path:`${id}` })
  }
  },
};
</script>
<style lang="less" scoped>
.post-title{
  text-align: center;

}

</style>
