<script>
export default {
  
  data() {
    return {
      menulist: [
          { id:0,authName:'Home',path:'Home', },
           { id:1,authName:'Article',path:'Article', },
            { id:3,authName:'Tags',path:'Tags' },
            { id:4,authName:'About',path:'About' },
            { id:5,authName:'Search' },
             { id:6,authName:'Lazyload',path:'Lazyload' },

      ],
      activepath: "",
      dialogVisible: false,
      search:'',
      newlist:[],
      value1: true,
    };
  },
    created() {
this.activepath = `/${this.$route.fullPath.split("/")[1]}`;
  },
  methods: {
  
    save(activepath) {
      if(activepath !== "/undefined"){
         this.activepath = activepath;
      }else{
        this.dialogVisible=true
      }
  
    },
  },
 watch: {

    search (val) {
 const list=this.$store.state.list
  console.log();
      this.newlist=[]
      if(val.trim().length !==0){
      list.forEach(item => {
     if(item.title.indexOf(val)===-1){ 
    
   }else{
      this.newlist.push({
      id:item.id,
      title:item.title,
      pathID:item.pathID
    })
  
  }
   });
      }else{
      }

    },
    value1(val){

      if(val){
        this.$refs.home.classList.remove("dark")
      }else{
         this.$refs.home.classList.add("dark")
      }
    }
  }
};
</script>
